import React, { useState, useEffect } from 'react';
import { Link as RouterLink, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import axios from "axios";

import {
    Grid,
    Link,
    Typography
} from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    root: {
        backgroundColor: theme.palette.background.default,
        height: '100%'
    },
    grid: {
        height: '100%'
    },
    quoteContainer: {
        [theme.breakpoints.down('md')]: {
            display: 'none'
        }
    },
    quote: {
        backgroundColor: theme.palette.neutral,
        height: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundImage: 'url(/images/auth.jpg)',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center'
    },
    quoteInner: {
        textAlign: 'center',
        flexBasis: '600px'
    },
    quoteText: {
        color: theme.palette.white,
        fontWeight: 300
    },
    comfText: {
        color: theme.palette.green,
        fontWeight: 200,
        paddingLeft: theme.spacing(2),
    },
    name: {
        marginTop: theme.spacing(3),
        color: theme.palette.white
    },
    bio: {
        color: theme.palette.white
    },
    contentContainer: {},
    content: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column'
    },
    contentHeader: {
        display: 'flex',
        alignItems: 'center',
        paddingTop: theme.spacing(5),
        paddingBototm: theme.spacing(2),
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2)
    },
    logoImage: {
        marginLeft: theme.spacing(4)
    },
    contentBody: {
        flexGrow: 1,
        display: 'flex',
        alignItems: 'center',
        [theme.breakpoints.down('md')]: {
            justifyContent: 'center'
        }
    },
}));

const Verified = props => {
    var token = props.match.params.token;

    axios
        .post("http://localhost:3000/users/activate", {
            token
        })
        .then((response) => {
            console.log('Verified');

        })
        .catch((error) => {
            console.log('why am i doing this project')
        });



    const classes = useStyles();
    return (
        <div className={classes.root}>
            <Grid
                className={classes.grid}
                container
            >
                <Grid
                    className={classes.quoteContainer}
                    item
                    lg={5}
                >
                    <div className={classes.quote}>
                        <div className={classes.quoteInner}>
                            <Typography
                                className={classes.quoteText}
                                variant="h1"
                            >
                                Join Us
              </Typography>
                            <div className={classes.person}>
                                <Typography
                                    className={classes.name}
                                    variant="body1"
                                >
                                    Take Control Of Your Health
                </Typography>
                                <Typography
                                    className={classes.bio}
                                    variant="body2"
                                >
                                    3ELM
                </Typography>
                            </div>
                        </div>
                    </div>
                </Grid>
                <Grid
                    className={classes.content}
                    item
                    lg={7}
                    xs={12}
                >
                    <div className={classes.content}>
                        <div className={classes.contentHeader}>
                        </div>
                        <div className={classes.contentBody}>
                            <Typography
                                className={classes.comfText}
                                variant="h3"
                            >
                                Your email has been verified! You may log in.

                              </Typography>
                            <Typography
                                color="textSecondary"
                                variant="body1"
                            >

                                <Link
                                    component={RouterLink}
                                    to="/sign-in"
                                    variant="h6"
                                >
                                    Sign in
                              </Link>
                            </Typography>
                        </div>
                    </div>
                </Grid>
            </Grid>
        </div>
    )
};

Verified.propTypes = {
    history: PropTypes.object
};

export default withRouter(Verified);
